body {
  font-family: "Pontano Sans";
}

.board {
  text-align: center;
}

.leader-board {
  margin: 40px;
  font-weight: bold;
  color: rgb(4, 52, 71);
}

.heading-backtest {
  margin: 12px;
  font-weight: bold;
  color: rgb(4, 52, 71);
  display: inline-block;
}

.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.change-slippage {
  display: flex;
}

.selector {
  border: 1px solid grey;
  border-radius: 8px;
}

.headings, .ranking {
  margin: auto;
  display: grid;
  min-width: 70%;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 10px;
}

.headings {
  font-weight: bold;
}

.first {
  background-color: goldenrod;
}

.second {
  background-color: silver;
}

.third {
  background-color: saddlebrown;
}
